"use client"

import React, { useState } from 'react';
import { Mail, Send, AlertCircle, CheckCircle, Loader2 } from 'lucide-react';

export default function BulkEmailSender() {
  const [recipients, setRecipients] = useState('');
  const [subject, setSubject] = useState('');
  const [message, setMessage] = useState('');
  const [sending, setSending] = useState(false);
  const [result, setResult] = useState(null);

  const parseRecipients = (text) => {
    const emails = text
      .split(/[,;\n\s]+/)
      .map(email => email.trim())
      .filter(email => email !== '');
    return emails;
  };

  const getRecipientCount = () => {
    const emails = parseRecipients(recipients);
    return emails.length;
  };

  const handleSend = async () => {
    const validRecipients = parseRecipients(recipients);
    
    if (validRecipients.length === 0) {
      setResult({ success: false, message: 'Please add at least one recipient' });
      return;
    }

    if (!subject.trim()) {
      setResult({ success: false, message: 'Please add a subject' });
      return;
    }

    if (!message.trim()) {
      setResult({ success: false, message: 'Please add a message' });
      return;
    }

    setSending(true);
    setResult(null);

    try {
      const response = await fetch('http://localhost:5000/api/send-bulk-email', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          recipients: validRecipients,
          subject,
          message,
        }),
      });

      const data = await response.json();

      if (response.ok) {
        setResult({ 
          success: true, 
          message: `Successfully sent ${data.successful} email(s). ${data.failed > 0 ? `Failed: ${data.failed}` : ''}` 
        });
        setRecipients('');
        setSubject('');
        setMessage('');
      } else {
        setResult({ success: false, message: data.message || 'Failed to send emails' });
      }
    } catch (error) {
      setResult({ success: false, message: 'Error connecting to server. Make sure backend is running on port 5000.' });
    } finally {
      setSending(false);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-purple-900 to-slate-900 p-6">
      <div className="max-w-5xl mx-auto">
        {/* Animated background elements */}
        <div className="fixed inset-0 overflow-hidden pointer-events-none">
          <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-purple-500 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-pulse"></div>
          <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-pink-500 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-pulse" style={{animationDelay: '2s'}}></div>
        </div>

        <div className="relative backdrop-blur-xl bg-white/10 rounded-3xl shadow-2xl overflow-hidden border border-white/20">
          {/* Header */}
          <div className="relative bg-gradient-to-r from-purple-600 via-pink-600 to-purple-600 p-8 overflow-hidden">
            <div className="absolute inset-0 bg-black/10"></div>
            <div className="relative flex items-center gap-4">
              <div className="p-3 bg-white/20 backdrop-blur-sm rounded-2xl">
                <Mail className="w-8 h-8 text-white" />
              </div>
              <div>
                <h1 className="text-4xl font-bold text-white tracking-tight">Bulk Email Sender</h1>
                <p className="text-purple-100 mt-1 text-sm">Send beautiful emails to multiple recipients effortlessly</p>
              </div>
            </div>
          </div>

          <div className="p-8 space-y-8">
            {/* Recipients Section */}
            <div className="group">
              <div className="flex items-center justify-between mb-4">
                <label className="text-lg font-semibold text-white flex items-center gap-2">
                  <span className="text-2xl">üìß</span>
                  Recipients
                  {getRecipientCount() > 0 && (
                    <span className="ml-2 px-3 py-1 text-xs font-bold bg-gradient-to-r from-purple-500 to-pink-500 text-white rounded-full shadow-lg animate-pulse">
                      {getRecipientCount()} email{getRecipientCount() !== 1 ? 's' : ''}
                    </span>
                  )}
                </label>
              </div>
              <textarea
                value={recipients}
                onChange={(e) => setRecipients(e.target.value)}
                placeholder="Paste email addresses here. Separate by comma, semicolon, space, or new line.&#10;&#10;Example:&#10;john@example.com, jane@example.com&#10;bob@example.com; alice@example.com"
                rows={8}
                className="w-full px-5 py-4 bg-white/10 backdrop-blur-sm text-white placeholder-purple-200/50 border border-white/20 rounded-2xl focus:outline-none focus:ring-2 focus:ring-purple-400 focus:border-transparent resize-none font-mono text-sm transition-all duration-300 hover:bg-white/15"
              />
              <p className="mt-3 text-sm text-purple-200/70 flex items-center gap-2">
                <span>‚ú®</span>
                <span>Paste from Excel, CSV, or any format - we'll handle the rest</span>
              </p>
            </div>

            {/* Subject */}
            <div className="group">
              <label className="text-lg font-semibold text-white mb-4 block flex items-center gap-2">
                <span className="text-2xl">‚úâÔ∏è</span>
                Subject
              </label>
              <input
                type="text"
                value={subject}
                onChange={(e) => setSubject(e.target.value)}
                placeholder="Enter your email subject..."
                className="w-full px-5 py-4 bg-white/10 backdrop-blur-sm text-white placeholder-purple-200/50 border border-white/20 rounded-2xl focus:outline-none focus:ring-2 focus:ring-purple-400 focus:border-transparent transition-all duration-300 hover:bg-white/15"
              />
            </div>

            {/* Message */}
            <div className="group">
              <label className="text-lg font-semibold text-white mb-4 block flex items-center gap-2">
                <span className="text-2xl">üìù</span>
                Message
              </label>
              <textarea
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                placeholder="Compose your message here..."
                rows={10}
                className="w-full px-5 py-4 bg-white/10 backdrop-blur-sm text-white placeholder-purple-200/50 border border-white/20 rounded-2xl focus:outline-none focus:ring-2 focus:ring-purple-400 focus:border-transparent resize-none transition-all duration-300 hover:bg-white/15"
              />
            </div>

            {/* Result Message */}
            {result && (
              <div className={`flex items-start gap-3 p-5 rounded-2xl backdrop-blur-sm border transition-all duration-300 ${
                result.success 
                  ? 'bg-green-500/20 text-green-100 border-green-400/30' 
                  : 'bg-red-500/20 text-red-100 border-red-400/30'
              }`}>
                {result.success ? (
                  <CheckCircle className="w-6 h-6 mt-0.5 flex-shrink-0" />
                ) : (
                  <AlertCircle className="w-6 h-6 mt-0.5 flex-shrink-0" />
                )}
                <p className="text-sm font-medium">{result.message}</p>
              </div>
            )}

            {/* Send Button */}
            <button
              onClick={handleSend}
              disabled={sending}
              className={`w-full relative group overflow-hidden flex items-center justify-center gap-3 px-8 py-5 rounded-2xl font-bold text-white text-lg transition-all duration-300 ${
                sending
                  ? 'bg-slate-600 cursor-not-allowed'
                  : 'bg-gradient-to-r from-purple-600 via-pink-600 to-purple-600 hover:shadow-2xl hover:shadow-purple-500/50 hover:scale-[1.02] active:scale-[0.98]'
              }`}
            >
              {!sending && (
                <div className="absolute inset-0 bg-gradient-to-r from-pink-600 via-purple-600 to-pink-600 opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>
              )}
              <span className="relative flex items-center gap-3">
                {sending ? (
                  <>
                    <Loader2 className="w-6 h-6 animate-spin" />
                    Sending Emails...
                  </>
                ) : (
                  <>
                    <Send className="w-6 h-6" />
                    Send Bulk Email
                  </>
                )}
              </span>
            </button>
          </div>
        </div>

        {/* Instructions */}
        <div className="mt-8 relative backdrop-blur-xl bg-white/10 rounded-2xl shadow-xl p-8 border border-white/20">
          <h2 className="text-2xl font-bold text-white mb-5 flex items-center gap-3">
            <span className="text-3xl">‚öôÔ∏è</span>
            Backend Setup Instructions
          </h2>
          <ol className="space-y-4 text-purple-100">
            <li className="flex gap-3">
              <span className="flex-shrink-0 w-7 h-7 bg-purple-500/30 rounded-full flex items-center justify-center text-sm font-bold">1</span>
              <span>Create a new folder for your backend and run: <code className="bg-black/30 px-3 py-1 rounded-lg font-mono text-pink-300">npm init -y</code></span>
            </li>
            <li className="flex gap-3">
              <span className="flex-shrink-0 w-7 h-7 bg-purple-500/30 rounded-full flex items-center justify-center text-sm font-bold">2</span>
              <span>Install dependencies: <code className="bg-black/30 px-3 py-1 rounded-lg font-mono text-pink-300">npm install express cors nodemailer dotenv</code></span>
            </li>
            <li className="flex gap-3">
              <span className="flex-shrink-0 w-7 h-7 bg-purple-500/30 rounded-full flex items-center justify-center text-sm font-bold">3</span>
              <span>Create the server.js file with the backend code</span>
            </li>
            <li className="flex gap-3">
              <span className="flex-shrink-0 w-7 h-7 bg-purple-500/30 rounded-full flex items-center justify-center text-sm font-bold">4</span>
              <span>Create a .env file with your email credentials</span>
            </li>
            <li className="flex gap-3">
              <span className="flex-shrink-0 w-7 h-7 bg-purple-500/30 rounded-full flex items-center justify-center text-sm font-bold">5</span>
              <span>Run the server: <code className="bg-black/30 px-3 py-1 rounded-lg font-mono text-pink-300">node server.js</code></span>
            </li>
          </ol>
        </div>
      </div>
    </div>
  );
}